import * as React from 'react';
import {ChipGroup} from '@here/hds-chip';

// eslint-disable-next-line no-new
new ChipGroup();

interface HDSChipGroupProperties extends React.HTMLAttributes<HTMLElement> {
  valign?: string;
  justify?: string;
  dragging?: boolean;
}

const HDSChipGroup = React.forwardRef((props: HDSChipGroupProperties, ref) => {
  const {children, className, valign, justify, dragging, ...rest} = props;

  return (
    <hds-chip-group
      {...(className && {class: className})}
      valign={valign}
      {...(justify && {justify})}
      {...(dragging && {dragging: true})}
      {...rest}
      ref={ref}
    >
      {children}
    </hds-chip-group>
  );
});

HDSChipGroup.defaultProps = {
  valign: 'center',
  justify: 'flex-start',
  dragging: false,
};

export default HDSChipGroup;
